<template>
  <div>
    <h2>Home组件</h2>
      <!-- 路由携带查询参数 -->
      <button @click="goState({name: 'x', query: {count: 5}})">x组件</button>
      <button @click="goState({name: 'y', query: {count: 10}})">y组件</button>
    <div>
      <!-- 三级路由 -->
      <router-view></router-view>

    </div>
  </div>
</template>

<script>
  export default {
    name: 'home',

    methods: {
      //根据不同路由查询参数获取数据
      goState(o) {
        return this.$router.push(o);
      }
    }
  }
</script>